import React, { useEffect, useState } from "react";
import { StyleSheet, Text, View, TouchableOpacity, Button, Modal, TouchableHighlight, Image } from "react-native";
import { Camera as ExpoCamera } from 'expo-camera';

import users from './userData';

interface userAuth {
"name": string,
    "imageUrl": string,
    "id": number,
    "auth": boolean,
    "token": string
}

const CameraShot: React.FC = () =>{
   
        const [hasPermission, setHasPermission] = useState(null);
        const [type, setType] = useState(ExpoCamera.Constants.Type.back);
        const [scanned, setScanned] = useState(false);
        const [modalVisible, setModalVisible] = useState(false);
        const [picked, setPicked] = useState<userAuth | null>(null);
      
        useEffect(() => {
          (async () => {
            const { status } = await ExpoCamera.requestPermissionsAsync();
            setHasPermission(status === 'granted');
          })();
        }, []);

        const handleBarCodeScanned = ({data }) => {
            const pickedUser = users.filter(user=> user.token === data)
                setPicked(pickedUser.length != 0 ? pickedUser[0]: null)
                setScanned(true);
                setModalVisible(true);   
        
        }
      
        if (hasPermission === null) {
          return <View />;
        }
        if (hasPermission === false) {
          return <Text>No access to camera</Text>;
        }
        return (
          <View style={styles.container}>
            <ExpoCamera style={styles.camera} type={type} onBarCodeScanned={scanned ? undefined : handleBarCodeScanned}>
            
              </ExpoCamera>
             
            
              <View style={styles.centeredView}>
      

    </View>

          </View>
        );
      }

      const styles = StyleSheet.create({
        container: {
          flex: 1,
          alignItems:"center",
        },
        camera: {
          height: 325,
          width: 325,
        },
        buttonContainer: {
          flex: 1,
          backgroundColor: 'transparent',
          flexDirection: 'row',
          margin: 20,
        },
        button: {
          flex: 1,
          alignSelf: 'flex-end',
          alignItems: 'center',
          backgroundColor:"#d5d5d5",
          borderRadius: 20,
          shadowColor: '#000',
            shadowOffset: {
              width: 3,
              height: 5,
            },
            shadowOpacity: 0.25,
            shadowRadius: 3.84,
        },
        text: {
          fontSize: 18,
          color: 'black',
        },
        

        centeredView: {
            flex: 1,
            justifyContent: 'center',
            alignItems: 'center',
            marginTop: 22,
          },
          modalView: {
          
            height: 250,
            width: 250,
            backgroundColor: 'white',
            borderRadius: 20,
            padding: 10,
            alignItems: 'center',
            shadowColor: '#000',
            shadowOffset: {
              width: 3,
              height: 5,
            },
            shadowOpacity: 0.25,
            shadowRadius: 3.84,
           
          },
          openButton: {
            margin: 10,
            backgroundColor: '#2196F3',
            borderRadius: 20,
            padding: 10,
            elevation: 2,
          },
          textStyle: {
            color: 'white',
            fontWeight: 'bold',
            textAlign: 'center',
          },
          modalText: {
            marginBottom: 15,
            textAlign: 'center',
          },
          image: {
            width: 90,
            height: 90,
            borderRadius:20,
            marginBottom:20,
        },
        icon:{
            marginTop:20,
        }
      });
      


export default CameraShot;