import React from "react";
import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";
import { Feather } from "@expo/vector-icons";
import { NavigationContainer } from "@react-navigation/native";
import Camera from "./pages/Camera";
import Main from "./pages/Main";
import CameraShot from "./pages/CameraShot";



const Tab = createBottomTabNavigator();

export default function Routes() {
  return (
    <NavigationContainer>
      <Tab.Navigator>
        <Tab.Screen options={{tabBarIcon: ({color})=> <Feather name="user" size={30} color= {color}/>}} name="Main" component={Main} />
        <Tab.Screen options={{tabBarIcon: ({color})=> <Feather name="camera" size={30} color= {color}/>}} name="Camera" component={Camera} />
        <Tab.Screen options={{tabBarIcon: ({color})=> <Feather name="camera" size={30} color= {color}/>}} name="CameraShot" component={CameraShot} />
      </Tab.Navigator>
    </NavigationContainer>
  );
}