import React from "react";
import { StyleSheet } from "react-native";
import { FlatList, View, Image, Text } from "react-native";
import users from './userData';

const Main: React.FC = () =>{
    

  

    return (
        <FlatList
            data={users}
            keyExtractor={user => user.name}
            renderItem={({item: users})=>(
                <View style={styles.user}>
                    <Image style={styles.image}  source={{uri : users.imageUrl}}/>
                    <Text>{users.name}</Text>
                </View>
            )}
        />
    );
}

const styles = StyleSheet.create({
    user: {
        flexDirection: 'row',
        alignItems: 'center',
        marginBottom: 20,
        marginLeft: 20,
        marginRight: 20,
        padding: 10,
        backgroundColor:"#d5d5d5",
          borderRadius: 20,
          shadowColor: '#000',
            shadowOffset: {
              width: 3,
              height: 5,
            },
            shadowOpacity: 0.25,
            shadowRadius: 3.84,
    },

    image: {
        width: 42,
        height: 42,
        marginRight:5,
        borderRadius:10,
    }
})

export default Main;
